body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  min-height: 200vh;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.basket__circle {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.basket__circle_header {
  width: 44px;
  height: 44px;
  background-color: #009baf;
}

.basket__circle_product {
  width: 40px;
  height: 40px;
  background-color: #dadada;
}

.basket__image {
  font-size: 24px;
  line-height: 0;
  color: #fff;
}

.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 14px 24px;
  overflow: hidden;
}

.product-card__labels-block {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.product-card__label {
  height: 22px;
  padding: 0 16px;
  border-radius: 11px;
  background-color: #f68038;
  font-size: 14px;
  font-weight: 900;
  line-height: 22px;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.product-card__link {
  font-size: 15px;
}

.product-card__image {
  margin-top: 25px;
}

.product-card__image img {
  width: 100%;
}

.product-card__title {
  color: #009baf;
  font-weight: 900;
}

.product-card__subtitle {
  color: #009baf;
  margin-bottom: 20px;
}

.product-card__old-price {
  align-self: flex-end;
  font-size: 18px;
  text-decoration-line: line-through;
  color: #999999;
}

.product-card__old-price::after {
  content: " ₽";
  font-size: 12px;
}

.product-card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-card__price {
  font-size: 24px;
  font-weight: 900;
  color: #000;
}

.product-card__price::after {
  content: " ₽";
  font-size: 18px;
}

.header {
  position: fixed;
  z-index: 20;
  width: 100%;
}

.header__up-block {
  height: 90px;
  background-color: #009baf;
  color: #fff;
}

.header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.header__menu {
  flex: 0 0 37%;
}

.header__center-element {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  display: flex;
  align-items: flex-end;
  width: 200px;
  height: 130px;
  padding-bottom: 40px;
  border-radius: 0% 0% 50% 50%/0% 0% 45% 45%;
  background-color: #009baf;
}

.header__down-block {
  height: 70px;
  background-color: #fff;
}

.menu {
  height: 100%;
  padding: 12px 0 20px;
}

.menu__icon {
  z-index: 10;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 30px;
  padding: 5px 0;
  cursor: pointer;
  overflow: hidden;
}

.menu__icon.active .menu__iconline:first-child {
  transform: translateY(9px) rotate(45deg);
}

.menu__icon.active .menu__iconline:nth-child(2) {
  width: 0;
  transform: translate(15px);
}

.menu__icon.active .menu__iconline:last-child {
  transform: translateY(-9px) rotate(-45deg);
}

.menu__icon-line {
  width: 100%;
  height: 2px;
  transition: 200ms;
}

.navigation {
  height: 100%;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  flex-wrap: wrap;
  height: 100%;
}

.navigation__item {
  align-self: flex-start;
  margin-right: 5px;
  border-radius: 14px;
  transition: 200ms;
}

.navigation__link {
  padding: 5px 22px;
  font-family: Play, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: underline;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  width: 100%;
}

.logo__image {
  width: 180px;
}

.logo__text {
  font-size: 12px;
  font-weight: 300;
  cursor: default;
}

.phones {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4%;
  flex: 0 0 38%;
  padding: 9px 40px 18px 0;
}

.phones__icon {
  font-size: 35px;
  line-height: 25px;
}

.phones__line {
  width: 1px;
  height: 30px;
  background-color: #fff;
}

.phones-block__location {
  font-size: 12px;
  font-weight: 300;
  cursor: default;
}

.phones-block__number {
  font-size: 18px;
  font-weight: 900;
}

.link {
  display: flex;
  align-items: center;
  gap: 8px;
}

.link__icon {
  color: #999999;
  font-size: 22px;
  line-height: 0;
  transition: 200ms;
}

.link__text {
  color: #999999;
  font-size: 14px;
  font-weight: 400;
  transition: 200ms;
  border-bottom: 1px dashed #999999;
}

.link_email .link__icon {
  font-size: 20px;
}

.link_email .link__text {
  border-bottom: 1px solid #999999;
}

.input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10%;
  flex: 0 0 37%;
  padding-left: 20px;
}

.interplay {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8%;
  flex: 0 0 40%;
  padding-right: 35px;
}

.interplay__basket {
  position: relative;
}

.interplay__amount {
  position: absolute;
  top: -8px;
  left: 28px;
  border-radius: 12px;
  padding: 4px 8px;
  background-color: #f68038;
  color: #fff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
}

.timetable {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: default;
}

.timetable__icon {
  color: #999999;
  font-size: 28px;
  line-height: 0;
}

.timetable__text {
  color: #999999;
}

.timetable__time_bold {
  font-weight: 900;
}

.main {
  margin-top: 165px;
}

.main__container {
  display: flex;
  gap: 30px;
  border: 1px solid lightgray;
  overflow: hidden;
}

.main__leftside {
  flex: 0 0 260px;
  min-height: 100vh;
}

.main__rightside {
  flex: 1 1 auto;
  border: 1px solid lightgray;
  min-height: 100vh;
}

.catalogbar {
  position: relative;
  width: 100%;
  height: 100px;
  color: #fff;
  transition: 200ms;
}

.catalogbar__head {
  position: relative;
  z-index: 10;
}

.catalogbar__body {
  position: absolute;
  z-index: 9;
  bottom: 0;
}

.catalogbar__panel {
  position: absolute;
  z-index: 8;
  top: 0;
  right: 0;
  right: -260px;
}

.catalogbar__panel.active {
  right: -260px;
}

.ctlg-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 15px 20px;
  background-color: #009baf;
}

.ctlg-head__title {
  flex: 0 1 65%;
  font-family: UniSansHeavyCaps, sans-serif;
  font-size: 30px;
  font-weight: 400;
  cursor: default;
}

.ctlg-head__button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 36px;
  height: 36px;
  background-color: #068292;
  cursor: pointer;
}

.ctlg-head__btnline {
  width: 24px;
  height: 2px;
  background-color: #fff;
}

.ctlg-body {
  width: 100%;
  background-color: #009baf;
}

.ctlg-body__link {
  width: 100%;
  height: 40px;
  padding-left: 20px;
  border-bottom: 1px solid #1ab9ce;
  font-size: 12px;
  font-weight: 300;
  line-height: 40px;
  cursor: pointer;
}

.ctlg-body__link_menu {
  position: relative;
}

.ctlg-body__link_menu::before {
  position: absolute;
  right: 10px;
  font-size: 16px;
  line-height: 40px;
  transition: 300ms;
}

.ctlg-panel {
  width: 260px;
  border: 2px solid #009baf;
  border-left: none;
  background-color: #fff;
}

.ctlg-panel__tipes {
  padding: 12px 0 12px;
  border-bottom: 2px solid #dadada;
  color: #1ab9ce;
}

.ctlg-panel__link {
  width: 100%;
  height: 36px;
  padding-left: 25px;
  line-height: 36px;
}

@media (min-width: 992px) {
  .navigation__item:hover {
    background-color: #068292;
  }

  .link:hover {
    color: #1ab9ce;
  }

  .link:hover .link__icon {
    color: #1ab9ce;
  }

  .link:hover .link__text {
    color: #1ab9ce;
    border-bottom: 1px dashed #1ab9ce;
  }

  .link_email:hover .link__text {
    border-bottom: 1px solid #1ab9ce;
  }

  .ctlg-body__link:hover {
    background-color: #068292;
  }

  .ctlg-panel__link:hover {
    background-color: #71c7d3;
    color: #fff;
  }
}

@media (max-width: 1140px) {
  .navigation__link {
    padding: 5px 15px;
    font-size: 12px;
  }

  .phones {
    flex: 0 0 37%;
    padding: 9px 15px 18px 0;
  }

  .phones__line {
    height: 25px;
  }

  .phones-block__location {
    font-size: 11px;
  }

  .phones-block__number {
    font-size: 14px;
  }

  .link {
    gap: 5px;
  }

  .link__icon {
    font-size: 20px;
  }

  .link__text {
    font-size: 12px;
  }

  .input {
    gap: 6%;
    padding-left: 10px;
  }

  .interplay {
    gap: 5%;
    flex: 0 0 38%;
    padding-right: 10px;
  }

  .interplay__amount {
    font-size: 12px;
    line-height: 12px;
  }

  .timetable {
    gap: 4px;
  }

  .timetable__icon {
    font-size: 24px;
  }

  .timetable__text {
    font-size: 9px;
  }
}

@media (max-width: 900px) {
  .menu {
    padding: 12px 0 16px;
  }

  .navigation__list {
    align-content: flex-start;
  }

  .navigation__link {
    padding: 0 10px;
  }

  .phones {
    justify-content: flex-end;
  }

  .phones__line {
    display: none;
  }

  .phones-block_msk {
    display: none;
  }

  .link__icon {
    font-size: 14px;
  }

  .link_email {
    display: none;
  }

  .input {
    gap: 10%;
    justify-content: flex-start;
  }

  .interplay {
    gap: 10%;
    justify-content: flex-end;
  }

  .interplay__link {
    display: none;
  }

  .timetable__icon {
    font-size: 20px;
  }
}

@media (max-width: 768px) {
  .header__up-block {
    height: 60px;
  }

  .header__center-element {
    align-items: center;
    height: 60px;
    padding-bottom: 0;
    border-radius: 0;
    background-color: transparent;
  }

  .header__down-block {
    height: 40px;
  }

  .menu {
    display: flex;
    align-items: center;
    padding: 0 0 0 15px;
  }

  .menu {
    display: flex;
    align-items: center;
    padding: 0 0 0 10px;
  }

  .menu__icon {
    display: flex;
  }

  .navigation {
    position: fixed;
    z-index: 5;
    top: 60px;
    left: -100%;
    width: 320px;
    height: 100%;
    background-color: rgba(7, 140, 158, 0.75);
    transition: 300ms;
  }

  .navigation.active {
    left: 0;
  }

  .navigation__list {
    justify-content: flex-start;
    gap: 26px;
    padding: 25px 30px;
  }

  .navigation__link {
    font-size: 18px;
    padding: 0;
  }

  .logo {
    gap: 2px;
  }

  .logo__image {
    width: 120px;
  }

  .logo__text {
    font-size: 10px;
  }

  .phones {
    padding: 0 10px 0 0;
  }

  .phones-block_rus {
    display: none;
  }

  .link_email {
    display: flex;
  }

  .link_email .link__icon {
    font-size: 14px;
  }

  .input {
    justify-content: space-between;
    gap: 0;
    flex: 0 0 100%;
    padding: 0 10px;
  }

  .interplay {
    display: none;
  }

  .interplay__link {
    display: flex;
  }

  .interplay__basket .basket__circle {
    width: 35px;
    height: 35px;
    background-color: transparent;
  }

  .interplay__basket .basket__image {
    font-size: 20px;
  }

  .interplay__amount {
    top: 0px;
    left: 20px;
    border-radius: 12px;
    padding: 3px 5px;
    font-size: 11px;
    line-height: 8px;
  }

  .main {
    margin-top: 105px;
  }
}

@media (max-width: 620px) {
  .header__down-block {
    height: 0;
  }

  .menu {
    padding: 0;
  }

  .phones {
    gap: 0;
    padding: 0;
  }

  .phones__icon {
    font-size: 27px;
  }

  .input {
    display: none;
  }

  .main {
    margin-top: 65px;
  }
}